<template>
    <div>
        <el-form ref="form" :model="form" label-width="110px">
            <el-row class="toolbar bgc-fff">
                <el-col :span="12">
                    <h2 class="fs-18">当前订单状态：<span  class="color-red">待提交(车辆订金阶段)</span></h2>
                    <p class="fs-14">当前处理人：admin</p>
                </el-col>
            </el-row>
            <el-collapse v-model="collapseActive" @change="">
                <el-collapse-item title="　车主信息" name="1">

                    <el-form-item label="车主名称">
                        {{form.sale_info.name}}
                    </el-form-item>

                    <el-form-item label="车主类型">
                        {{form.sale_info.type}}
                    </el-form-item>

                    <el-form-item label="移动电话">
                        {{form.sale_info.mobile}}
                    </el-form-item>

                    <el-form-item label="性别">
                        {{form.sale_info.gender==1?'男':'女'}}
                    </el-form-item>

                    <el-form-item label="证件号">
                        {{form.sale_info.id_card}}
                    </el-form-item>

                    <el-form-item label="销售单编号">
                        {{form.sale_info.sale_num_code}}
                    </el-form-item>
                    
                </el-collapse-item>

                <el-collapse-item title="　商品信息" name="2">
                    <el-table :data="form.car_info.rows" border class="table" ref="multipleTable">
                        <el-table-column label="商品信息" width="180">
                            <template slot-scope="{row, $index}">
                                <p>{{row.car_series}} {{row.car_brand}}</p>
                                <p>{{row.horse_power}} {{row.drive_form}} {{row.car_class}}</p>
                                <p>{{row.fuel_type}}</p>
                            </template>
                        </el-table-column>
                        <el-table-column prop="color" label="颜色" width="120">
                        </el-table-column>
                        <el-table-column prop="sale_amount" label="采购单价">
                        </el-table-column>
                        <el-table-column prop="num" label="数量" width="150"></el-table-column>
                        <el-table-column prop="total_price" label="小计">
                        </el-table-column>
                    </el-table>
                    <!-- <div class="pagination">
                        <el-pagination background @current-change="" layout="prev, pager, next" :total="1000">
                        </el-pagination>
                    </div> -->
                    <div class="toolbar ta-r mt10">
                        <span>共选择<b class="color-red fs-16"> {{goodsTotal}} </b>件商品</span>
                        <div class="input-coupon-sum">
                            优惠金额：{{form.sale_info.discount_amount}}
                        </div>
                        <el-tag type="danger">￥：{{form.refund_info.total_amount}}</el-tag>
                    </div>
                </el-collapse-item>

                <el-collapse-item title="　车主已支付信息" name="3">

                    <el-card class="box-card mb15">
                        <div><span class="display-ib ta-r w100">车主付款对象：</span>{{form.sale_info.dj.pay_object==2?'公司':'其他销售方'}}</div>
                        <div><span class="display-ib ta-r w100">车主支付方式：</span>{{form.sale_info.dj.pay_method==1?'现金':'转账'}}</div>
                        <div><span class="display-ib ta-r w100">首付比例：</span>{{form.sale_info.dj.payment_ratio}}</div>
                        <div><span class="display-ib ta-r w100">费用金额：</span>{{form.sale_info.dj.cost_amount}}</div>
                        <div>
                            <span class="display-ib ta-r w100 pull-left">费用凭证：</span>
                            <ul class="c-image-list">
                                <li><img :src="form.sale_info.dj.cost_voucher_img" alt=""></li>
                            </ul>
                        </div>
                        <div><span class="display-ib ta-r w100">备注：</span>{{form.sale_info.dj.sale_remarkes}}</div>
                    </el-card>

                </el-collapse-item>

                <el-collapse-item title="　退款信息" name="4">

                    <el-card class="box-card mb15">
                        <div><span class="display-ib ta-r w100">退款金额：</span>{{form.refund_info.refund_amount}}</div>
                        <div><span class="display-ib ta-r w100">退款银行：</span>{{form.refund_info.refund_banck}}</div>
                        <div><span class="display-ib ta-r w100">退款账号：</span>{{form.refund_info.refund_banck_num}}</div>
                        <div><span class="display-ib ta-r w100">备注：</span>{{form.refund_info.refund_remarks}}</div>
                    </el-card>

                    <template v-if="isShowRefund()">
                        <el-form-item label="费用凭证">
                            <el-upload
                                :disabled="isDisabledRefund()"
                                class="avatar-uploader"
                                :action="IMG_UPLOAD_URL"
                                :show-file-list="false"
                                :on-success="handleUploadSuccessRefund"
                                :before-upload="handleBeforeUpload">
                                <img v-if="form.refund_info.cost_voucher_img" :src="form.refund_info.cost_voucher_img" class="avatar">
                                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                            </el-upload>
                        </el-form-item>

                        <el-form-item label="备注">
                            <el-input type="textarea" v-model="form.refund_info.cost_remark" :disabled="isDisabledRefund()"></el-input>
                        </el-form-item>
                    </template>

                </el-collapse-item>

                <el-collapse-item title="　审核信息" name="5">

                    <el-card class="box-card mb15">
                        <div><span class="display-ib ta-r w100">车主付款对象：</span>定金审核 - 财务</div>
                        <div><span class="display-ib ta-r w100">车主支付方式：</span>通过</div>
                        <div><span class="display-ib ta-r w100">首付比例：</span>撒打算</div>
                        <div><span class="display-ib ta-r w100">费用金额：</span>2018-10-23 00:49:38</div>
                        <div>
                            <span class="display-ib ta-r w100 pull-left">费用凭证：</span>
                            <ul class="c-image-list">
                                <li><img :src="'./img/img.jpg'" alt=""></li>
                                <li><img :src="'./img/img.jpg'" alt=""></li>
                            </ul>
                        </div>
                    </el-card>


                    <el-form-item label="审核意见" class="mt15">
                        <el-select v-model="formAudit.exec_op_type" placeholder="请选择" class="mr10">
                            <el-option label="通过" value="1"></el-option>
                            <el-option label="驳回" value="2"></el-option>
                        </el-select>
                    </el-form-item>
                    <template v-if="isShowFinance()">
                        <el-form-item label="退款金额">
                            <el-input v-model="formFinance.refund_amount"></el-input>
                        </el-form-item>
                        <el-form-item label="退款银行">
                            <el-input v-model="formFinance.refund_banck"></el-input>
                        </el-form-item>
                        <el-form-item label="银行开户名">
                            <el-input v-model="formFinance.account_name"></el-input>
                        </el-form-item>
                        <el-form-item label="退款帐号">
                            <el-input v-model="formFinance.refund_banck_num"></el-input>
                        </el-form-item>
                    </template>
                    
                    <el-form-item label="备注信息">
                        <el-input type="textarea" v-model="formAudit.exam_remarks"></el-input>
                    </el-form-item>
                    <el-form-item label="选择审核人">
                        <el-select v-model="form.company" placeholder="请选择公司" class="mr10" @change="handleSelectAuditor">
                            <el-option
                                v-for="item in optionsCompany"
                                :key="item.id"
                                :label="item.mc_name"
                                :value="item.id">
                            </el-option>
                        </el-select>
                        <el-select v-model="formAudit.after_user_id" placeholder="请选择审核人">
                            <el-option
                                v-for="item in optionsExecUser"
                                :key="item.id"
                                :label="item.user_name"
                                :value="item.id">
                            </el-option>
                        </el-select>
                    </el-form-item>

                </el-collapse-item>
            </el-collapse>
            <el-form-item class="form-submit">
                <el-button type="primary" @click="handleSubmit(1)" v-if="pageType != 'view'">提交</el-button>
                <el-button @click="$router.back()">返回</el-button>
            </el-form-item>
        </el-form>
        
    </div>
</template>

<script>
    import * as API from '@/api/salesManagement'
    import { requestUrl } from '@/api/config'
    import { organizationListUl } from '@/api/enterpriseManagement'
    export default {
        name: 'baseform',
        data: function(){
            return {
                img:'',
                re:'',
                IMG_UPLOAD_URL: `${requestUrl}/home/UpLoadFile/uploadImg`,
                collapseActive: ['1'],
                pageType: false,
                optionsExecUser:[],
                optionsCompany:[],
                formFinance:{
                    refund_amount:'', // 退款金额
                    refund_banck:'', //      退款银行
                    refund_banck_num:'', //  退款银行账户
                    account_name:'', //  退款银行账户开户名
                },
                formAudit: {
                    exec_op_type: '',
                    exam_remarks: '',
                    after_user_id: '',
                },
                form: 
{
    sale_info: {
        sale_info_id: "",
        cus_user_id: "",
        type: "",
        discount_amount: "",
        sale_num_code: " ",
        node_id: "",
        work_id: "1",
        work_status: "2",
        dj: {
            ser_charge: "2",
            cheques_type: "2",
            pay_type: "3",
            pay_method: "2",
            pay_object: "2",
            payment_ratio: "1",
            credit_amount: 0.01,
            payment_money: 0.01,
            cost_amount: 2321.31,
            cost_voucher_img: "http://zhonghuan-1257386775.cos.ap-beijing.myqcloud.com/development/image/85794b4b810986d3438d5a66c18f4ecd.png",
            sale_remarkes: "123213"
        },
        name: "",
        gender: "",
        mobile: "",
        id_card: ""
    },
    car_info: {
        rows: [
            {
                order_list_id: "14",
                sale_info_id: "28",
                car_info: "",
                mcar_id: "1598",
                color: "fgfdgfd",
                num: "2",
                purchase_node: "",
                word_status: "3",
                car_brand: "长丰汽车",
                car_series: "飞铃",
                car_class: "皮卡",
                foreign_model: "3443534",
                fuel_type: "7",
                horse_power: "4354353",
                drive_form: "16",
                sale_amount: 0,
                car_status: "1",
                total_price: 0
            }
        ],
        total: "1",
        page: 1,
        offset: 10
    },
    refund_info: {
        refund_id: "2",
        sale_info_id: "28",
        sale_node: " XS15422669066447",
        car_num: "2",
        total_amount: 0,
        refund_amount: 0,
        refund_banck: "",
        refund_banck_num: "",
        refund_remarks: "",
        cost_remark: "",
        cost_voucher_img: "",
        why_refund: "1234",
        factors_img: [
            {
                uid: "1542273352507",
                url: "http://zhonghuan-1257386775.cos.ap-beijing.myqcloud.com/development/image/c7d2afd165baac9bd8ddecb865dcbf73.png",
                status: "success"
            }
        ]
    }
}

                ,
            }
        },
        computed:{
            goodsTotal(){
                let zong = 0;
                this.form.car_info.rows.forEach((item, index)=>{
                    zong += 1*item.num
                })
                return zong;
            }
        },
        created() {
            this.getFormData();
            
        },
        methods: {
            isDisabledRefund(){
                return this.form.sale_info.node_id!=17
            },
            isShowFinance(){
                return this.form.sale_info.node_id==14 && this.formAudit.exec_op_type==1
            },
            isShowRefund(){
                return this.form.sale_info.node_id>=17
            },
            getFormData() {
                let _path = this.$route.path,
                    _query = this.$route.query;
                _path.indexOf('view') > -1 ? this.pageType='view' : 'edit'

                // 获取退款信息
                API.salesManagementGetRefund({sale_id:_query.id}).then(res => {
                    if(res.code == 0){
                        Object.assign(this.form, res.data);
                        this.getExecUser();
                    }else{
                        this.$message.error(res.msg)
                    }
                    
                })

                // 公司列表
                organizationListUl({}).then(res => {
                    if(res.code == 0){
                        this.optionsCompany = res.data
                    }else{
                        this.$message.error(res.msg)
                    }
                })
            },
            getExecUser(){
                // 审核人
                let params = {
                    cp_id:this.company,
                    work_id:1,
                    node_id: this.form.sale_info.node_id,
                }
                API.salesManagementGetExecUser(params).then(res => {
                    if(res.code == 0){
                        this.optionsExecUser = res.data
                    }else{
                        this.$message.error(res.msg)
                    }
                })
            },
            handleSelectAuditor(val){
                this.getExecUser();
            },
            handleSubmit(){
                if(this.form.sale_info.node_id==14){
                    let params = {
                        type: 'refund',
                        sale_id:this.form.sale_info.sale_info_id,
                        ...this.formAudit,
                        ...this.formFinance
                    }
                    API.salesManagementUpdateRefund(params).then(res => {
                        if(res.code == 0){
                            this.$message.success('提交成功！')
                            this.$router.back()
                        }else{
                            this.$message.error(res.msg)
                        }
                    })
                }else if(this.form.sale_info.node_id==17){
                    let params = {
                        type: 'refund',
                        sale_id:this.form.sale_info.sale_info_id,
                        ...this.formAudit,
                        cost_remark: this.form.refund_info.cost_remark,
                        cost_voucher_img: this.form.refund_info.cost_voucher_img,
                    }
                    API.salesManagementUpdateRefund(params).then(res => {
                        if(res.code == 0){
                            this.$message.success('提交成功！')
                            this.$router.back()
                        }else{
                            this.$message.error(res.msg)
                        }
                    })
                }else{
                    let params = {
                        type: 'refund',
                        id:this.form.sale_info.sale_info_id, //  订单id
                        ...this.formAudit
                    }
                    API.salesManagementSubmitSale(params).then(res => {
                        if(res.code == 0){
                            this.$message.success('提交成功！')
                            this.$router.back()
                        }else{
                            this.$message.error(res.msg)
                        }
                        
                    })
                }
            },
            handleUploadSuccessRefund(res, file, fileList){
                this.form.refund_info.cost_voucher_img = res.data.url
            },
            handleBeforeUpload(){

            },
        }
    }
</script>

<style lang="scss" scoped>
    
</style>